import React from 'react'
import { useParams } from 'react-router-dom'
import { Characters } from '../Characters';

const CharacterScreem = ({history}) => {
    const {id} = useParams();

    const {type, name, description} = Characters.find(
        (character)=>character.id === id
        );

    const path = `/assets/${type}-${id}.png`

    const handleBack =()=>{
        history.goBack()
    }

    return (
        <div className='container row mt-5'>
            
            <div className='col-8 text-center'>
                <img src={path} alt={id} className='img-thumbnail'
                 style={{
                    width: "60%",
                    height: "450px",
                }}/>
            </div>
            <div className='col-4'>
                <h2>Name: {name}</h2>
                <p>Description: {description}</p>

                <button onClick={handleBack} className='btn btn-outline-primary'>Go Back</button>
            </div>

        </div>
    )
}

export default CharacterScreem
